const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list: []
		}
	},
	onLoad() {
		// 加载关注列表
		this.getList()
	},
	methods: {
		getList() {
			// 加载关注列表
			uni.showLoading({
			  title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/fans`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == '1') {
						this.list = res.data.data
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			})
		},
		delFollows(e) {
			let id = e.currentTarget.dataset.id
			let index = e.currentTarget.dataset.index
			uni.showLoading({
				title: '执行中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/user/del_follow`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: {
					follow_id: id
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res)
					if (res.data.status_code == '1') {
						this.list.splice(index, 1)
						uni.showToast({
							title: res.data.message
						});
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			})
		}
	}
}
